.menu-button {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
}

.menu-button__burger-menu {
  align-items: center;
  width: 24px;
  height: 16px;
  display: flex;
  position: relative;
}

.menu-button__burger-menu:before {
  content: "";
  background-color: #292929;
  border-radius: 2px;
  width: 100%;
  height: 2.5px;
  transition: all .3s ease-in-out;
  display: block;
  position: absolute;
  top: 0;
}

.menu-button__burger-menu:after {
  content: "";
  background-color: #292929;
  border-radius: 2px;
  width: 100%;
  height: 2.5px;
  transition: all .3s ease-in-out;
  display: block;
  position: absolute;
  top: 100%;
  transform: translateY(-100%);
}

.menu-button__burger-menu--close:before {
  top: 50%;
  transform: rotate(45deg)scale(.9);
}

.menu-button__burger-menu--close:after {
  top: 50%;
  transform: rotate(-45deg)scale(.9);
}

.menu-button__burger-menu--line {
  background-color: #292929;
  border-radius: 2px;
  width: 100%;
  height: 2.5px;
  transition: all .3s ease-in-out;
  display: block;
  position: absolute;
}

.menu-button__burger-menu--close .menu-button__burger-menu--line {
  opacity: 0;
}

